.posts{
  display: flex;
  justify-content: space-between; 
  -webkit-box-shadow: 0 5px 2px -2px rgb(129, 106, 72);;
  -moz-box-shadow: 0 5px 2px rgb(129, 106, 72);;
  box-shadow: 0 5px 2px -1px rgb(129, 106, 72);
}

.all-posts {
   display: flex;
   justify-content: space-between;
}
.main-posts {
    margin: 10px;
    padding: 10px;
}
.main-posts h1 {
    text-align: center;
  font-size: 20px;
  color: #3a3022;
  font-weight: 700;
  font-family: "Sacramento", cursive;
  margin: 15px;
}
